
body{
    box-sizing: border-box;
}
.home-container{  
   
   width:100%;
    height: 100vh;
   background-image: url(../images/homebc.jpg);
   background-size: cover;
   text-align: left;
   font-family: Arial, Helvetica, sans-serif;
}
.home-container h1{

    margin:0;
    padding: 0px;
    height:60px;
    font-weight:50px;
    color: rgba(245, 222, 222, 0.7);
}
.home-content{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 300px;
    
    
}
.home-container p{
   margin:0;
    padding:0;
    font-weight: lighter;
    font-size: 40px;
    color: rgba(245, 222, 222, 0.7);
}
.home-container button{
    background-color: white;
    width: 200px;
    border-radius: 5px;
    margin-top: 30px;
    padding: 10px;
    border:1px solid transparent;
    font-weight: bolder;
    

}
.home-container button:hover{
    background-color: rgb(225,225,225);
    cursor: pointer;
    transition: 0.3s ease-in;

}
@media screen and (max-width: 600px){
    .home-content{
        margin: 0;
        margin-left: 50px;
    }
    .home-content p{
        margin: 5px;
        font-size: 20px;
    }

    
}