body{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: sans-serif;
}
.contact-container{
    width:100%;
    height:100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.contact-container p{
    margin: 0;
    color:red;
    font-size: 13px;
    font-style:italic;
    font-weight: bold;
}
.contact-image{
    width:60%;
    height:100%;
}

.contact-image img{
    width:100%;
    height:100%;
}
.contact-content{
    border-radius: 5px;
    width:50%;
    height:400px;
    margin:20px;
    padding: 20px;
    box-shadow: 0px 3px 15px black;
    text-align: center;

}
.contact-content h1{
    margin: 0;
    padding: 20px;
}
.label{
    padding: 12px 12px 12px 0;
    color: rgba(0,0,0,0.5);
    
}
.contact-content input[type=text],textarea{
    height: 20px;
    width:70%;
    outline: none;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
    transition: 0.2s ease-in-out;
}

textarea{
   height: 60px;

}
.message-input,.email-input,.name-input{
    padding:0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;  
}
.message-input{
    padding: 5px;
}
.contact-submit{
    padding: 20px;

}
.contact-submit input[type=submit]
{
    background-color:rgba(0,0,0,0.5);
    width:50%;
    padding: 10px;
    border-radius: 5px;
    display: table-caption;
    color: white;
    border: none;
    cursor: pointer;
}
.contact-container input[type=text]:focus,textarea:focus{
    background-color:lightgray;
}
.contact-container input[type=submit]:hover{
background-color: black; 
}

@media screen and (max-width: 800px){
    .contact-image{
        display: none;
      
    }
    .contact-content{
        width: 100%;
        margin-top: 100px;
        
    }
   
}

