.body{
    box-sizing: border-box;
    
}
.about-container{ 
    
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
   
    
}
.about-image {
    margin-top:80px;   
    width:100%;
    height:300px;
    position: relative;
    top:20px;
}
.about-content{
    margin-top:50px;
    width:100%;
    height: auto;
    overflow-y: scroll;
}
.about-image img{
    width:65%;
    height: 100%;
}
.about-content h3{
    margin: 0;
}
.about-content p{
    margin: 0;
    text-align: justify;
    padding: 20px;
    font-family: Tahoma, sans-serif;
   
}
